



















body {
  overflow: hidden;
  font-family: Microsoft JhengHei;
}
body .Search {
    width: 100vw;
    height: 100vh;
}
body .Search #InpuText {
      margin: 2%;
      -moz-placeholder-font-size: 16px;
      -moz-placeholder-color: #9b9b9b;
}
body .Search #InpuText input {
        width: 83%;
        height: 60px;
        padding-left: 20px;
        font-size: 16px;
        color: #000;
        margin-right: 30px;
        border-radius: 5px;
        border-style: none;
        -webkit-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2);
                box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2);
}
body .Search #InpuText ::-webkit-input-placeholder {
        font-size: 16px;
        color: #9b9b9b;
}
body .Search #InpuText ::-moz-placeholder {
        font-size: 16px;
        color: #9b9b9b;
}
body .Search #InpuText button {
        width: 12%;
        line-height: 20px;
        background-color: #6c47f6;
        border-radius: 5px;
        -webkit-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2);
                box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2);
}
body .Search #InpuText button p {
          color: #FFF;
          font-size: 20px;
}
body .Search #Box {
      margin: 3% 2%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
body .Search #Box #Original {
        min-width: 30%;
        max-width: 30%;
        min-height: 100%;
        z-index: 1;
        margin-top: -45px;
}
body .Search #Box #Original .title {
          font-size: 23px;
          color: #000;
          margin-bottom: 10px;
}
body .Search #Box #Original .title span {
            margin-left: 10px;
            font-size: 14px;
            color: #4a4a4a;
}
body .Search #Box #Original #content_box {
          min-width: 35%;
          min-height: 70vh;
          max-height: 70vh;
          padding: 3vh 2vw;
          border-radius: 8px;
          -webkit-box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.2);
                  box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.2);
          background-color: #fff;
}
body .Search #Box #Original #content_box .content_text {
            max-height: 70vh;
            overflow: hidden;
            text-overflow: ellipsis;
}
body .Search #Box #Original #content_box .content_text .ContentTitle {
              font-size: 20px;
              color: #000;
              margin-bottom: 10px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
}
body .Search #Box #Original #content_box .content_text .date {
              font-size: 14px;
              color: #4a4a4a;
              margin-bottom: -10px;
}
body .Search #Box #Original #content_box .content_text .text {
              font-size: 14px;
              color: #4a4a4a;
              line-height: 14px;
              margin-bottom: 12px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
}
body .Search #Box #Original #content_box .content_text .text span {
                color: #f5a623;
}
body .Search #Box #other {
        width: 100%;
        height: 100%;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}
body .Search #Box #other .content_box {
          width: 30vw;
          height: 5vh;
}
body .Search #Box #other .content_box .content_title {
            width: 100vw;
            margin-left: 70px;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
}
body .Search #Box #other .content_box .content_title h1 {
              margin: 0px 0px 1px 0px;
              min-width: 33vw;
              font-size: 23px;
}
body .Search #Box #other .content_box .content_title h1 span {
                margin-left: 10px;
                font-size: 14px;
                color: #4a4a4a;
}
body .Search #Box #other .content_box .content {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            width: 100vw;
            height: 72vh;
            background-color: #f9f9f9;
}
body .Search #Box #other .content_box .content .move {
              display: -webkit-box;
              display: -ms-flexbox;
              display: flex;
              margin-left: 40px;
}
body .Search #Box #other .content_box .content .move .content_text {
                width: 33vw;
                height: 70vh;
                margin-top: 0.7vh;
                margin-left: 0px;
                overflow: hidden;
                text-overflow: ellipsis;
}
body .Search #Box #other .content_box .content .move .content_text .ContentTitle {
                  font-size: 20px;
                  color: #2286fd;
                  margin-bottom: 10px;
                  width: 27vw;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
}
body .Search #Box #other .content_box .content .move .content_text .date {
                  font-size: 14px;
                  color: #4a4a4a;
                  margin-bottom: -10px;
                  width: 27vw;
}
body .Search #Box #other .content_box .content .move .content_text .text {
                  font-size: 14px;
                  color: #4a4a4a;
                  line-height: 14px;
                  margin-bottom: 12px;
                  width: 27vw;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
}
body .Search #Box #other .content_box .content .move .content_text .text span {
                    color: #f5a623;
}
body .Search #Box #other .content_box .content .but_box {
              width: 30px;
              line-height: 70vh;
}
body .Search #Box #other .content_box .content .but_box button {
                width: 30px;
                height: 120px;
                border-style: none;
                padding: 0;
}
body .Search #Box #other .content_box .content .but_box button img {
                  width: 30px;
                  height: 120px;
}
body .Search #Box #other .content_box .content .right {
              z-index: 100;
              position: absolute;
              right: 0px;
}
body .Search #Box #other .content_box .content .left {
              z-index: 100;
}
